import React from 'react'
import { Button, Card, CardBody, CardText, CardTitle} from "reactstrap"
import { FaMapMarkedAlt, FaPhoneAlt } from "react-icons/fa"
import { MdEmail } from "react-icons/md"
import "bootstrap/dist/css/bootstrap.min.css"
import "./App.css"

export default function MyCard({details}) {
    return (
        <Card className="text-center my-card">
            <CardBody>
               
                    <img height="150" width="150" className="rounded-circle img-thumbnail  border-danger" src={details.picture?.large} />
                
                <CardTitle className="text-primary">
                    <h1>
                        <span> {details.name?.title}</span>
                        <span> {details.name?.first}</span>
                        <span> {details.name?.last}</span>
                    </h1>
                </CardTitle>
                <CardText>
                    <FaMapMarkedAlt /><span>{details.location?.city}</span><br/>
                    <FaPhoneAlt /> <span>{details.phone}</span>
                    <h4> <MdEmail /><span>{details.email}</span></h4>
                    <h5><Button className="btn-grad">Age : {details.dob?.age}</Button></h5> 
                </CardText>
            </CardBody>
       </Card>
    )
}
